import {myDom} from './myDom.js';
import Clear from './images/Clear.png';
import HeavyRain from './images/HeavyRain.png';


export const weatherInformation =(()=>{

            function weatherDomManipulation(city){

                    fetch(`http://api.openweathermap.org/data/2.5/weather?q=${city.name}&APPID=3eadc6de51c6bdb72de9cbc81db40285`)
                    .then(data=>data.json())
                    .then(data=>{
                        myDom.loader.style.display = 'none';
                        myDom.body.textContent = ''
                        buildWeatherForcastPage(city)   
                    })
                   
           }

            function buildWeatherForcastPage(city){
            const allItems = document.createElement('section')
                  allItems.setAttribute('class','allItems');

                //create the siebar
                const sideBar = document.createElement('div');
                      sideBar.setAttribute('class','sideBar');
                      const backArow = document.createElement('i')
                            backArow.setAttribute('class','fas fa-left-long');
                            backArow.addEventListener('click',()=>{
                                  myDom.body.textContent = ''
                                  myDom.body.append(myDom.header,myDom.filterSection,myDom.country_card);
                                  
                              })
                      const searchBar = document.createElement('div');
                             searchBar.setAttribute('class','searchBar');
                             const searchInput = document.createElement('input');
                                   searchInput.setAttribute('placeholder','search city...')
                                   
                             const searchBtn = document.createElement('button');
                                   searchBtn.textContent = 'search';
                                   searchBtn.addEventListener('click',()=>{
                                    fetch(`http://api.openweathermap.org/data/2.5/weather?q=${searchInput.value}&APPID=3eadc6de51c6bdb72de9cbc81db40285`)
                                    .then(data=>data.json())
                                    .then(data=>{
                                          console.log(data)
                                          myDom.body.textContent = ''
                                          buildWeatherForcastPage(data)
                                          myDom.loader.style.display = 'none';   
                                    })
                                    .catch(()=>{
                                          const h2 = document.createElement('h2');
                                                h2.textContent = 'Invalid search or slow internet connection'
                                                h2.classList.add('errorBox')
                                                myDom.body.textContent =''
                                                const backBtn = document.createElement('button')
                                                      backBtn.setAttribute('class','errorbackbtn');
                                                      backBtn.textContent = 'Back'
                                                      backBtn.addEventListener('click',()=>{
                                                            myDom.body.textContent = ''
                                                            myDom.body.append(myDom.header,myDom.filterSection,myDom.country_card);
                                                            
                                                            })
                                                h2.append(backBtn)

                                               
                                          myDom.body.append(h2)
                                      })

                                   })
                            searchBar.append(searchInput,searchBtn);
                      const images = document.createElement('div');
                            images.setAttribute('class','images')
                             const img1 = document.createElement('img')
                                   img1.src = Clear;
                             const img2 = document.createElement('img')
                                   img2.src = HeavyRain;
                        images.append(img1,img2);

                       const todayTemp = document.createElement('h1');
                             const degree = document.createElement('span');
                                degree.textContent = 'C';
                                degree.setAttribute('class','degree');
                                todayTemp.textContent = `${(Number(city.main.temp)-273.15).toFixed(0)}`;
                                todayTemp.appendChild(degree);
                        const shower= document.createElement('h2');
                              shower.textContent = `${city.weather[0].main}`;

                         const day = document.createElement('div');
                               const date = document.createElement('p');
                               let now = new Date().toLocaleDateString('en-us', { weekday:"long", month:"short", day:"numeric"});
                               date.textContent = `${now}`
                                    //  date.textContent = 'today . Fri. 5 jun'
                               const location = document.createElement('p');
                                    location.innerHTML = `<i class="fa-solid fa-location-dot"></i>${city.name}`;
                                    location.setAttribute('class','location');
                              day.append(date,location);
                sideBar.append(backArow, searchBar,images,todayTemp,shower,day);



            //     create the main weather info  section
            const container = document.createElement('section');
                  container.setAttribute('class','mainSection');

                  //      create wind status box
                       const box1 = document.createElement('div');
                              const wind = document.createElement('p');
                                    wind.textContent = 'Wind Status';
                              const amount = document.createElement('h1');
                                    amount.innerHTML = `<span class="wind">${(city.wind.speed).toFixed(1)}</span>mph`;
                              box1.append(wind,amount); 
                        
                    //      create humidity status box
                    const box2 = document.createElement('div');
                          box2.setAttribute('class','humidityBox')
                    const humidity = document.createElement('p');
                          humidity.textContent = 'Humidity';
                    const humidtyStatus = document.createElement('h1');
                          humidtyStatus.innerHTML = `<span class="humadity">${city.main.humidity}</span>%`;
                    const range = document.createElement('div')
                          range.setAttribute('class', 'range'); 
                          const innerrange = document.createElement('div')
                          innerrange.setAttribute('class', 'innerrange');
                          innerrange.style.width = `${city.main.humidity}%`;
                          range.append(innerrange);       
                   box2.append(humidity,humidtyStatus,range);
                   

                   // create humidity status box
                    const box3 = document.createElement('div');
                    const visibility = document.createElement('p');
                          visibility.textContent = 'Visibility';
                    const visibilityStatus = document.createElement('h1');
                          visibilityStatus.innerHTML = '<span class="humadity">6,4</span>miles';
                      
                   box3.append(visibility,visibilityStatus);

                   const box4 = document.createElement('div');
                   const pressure = document.createElement('p');
                         pressure.textContent = 'Air Pressure';
                   const pressureStatus = document.createElement('h1');
                         pressureStatus.innerHTML = `<span class="humadity">${city.main.pressure}</span>mb`;
                     
                  box4.append(pressure,pressureStatus);
                  const heading = document.createElement('h1')
                  heading.textContent = "Today's weather information"

                  container.append(heading,box1,box2,box3,box4)
                   
                 
                  allItems.append(sideBar,container)
                myDom.body.append(allItems);   

           }
            
            function showWeatherInfo(city){
                    myDom.MainContainer.textContent = '';
                    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=3eadc6de51c6bdb72de9cbc81db40285`)
                    .then(data=>data.json())
                    .then(data=>{
                        myDom.loader.style.display = 'none';
                        weatherDomManipulation(data)
                    });
                
            }
            

            return{
                showWeatherInfo,
                weatherDomManipulation,
            }
})()
