import { countryApi } from "./apiCalls";
import {weatherInformation} from './weatherinfo.js'
export const myDom = (()=>{

    const options = document.querySelector('.options');
    const filter_btn = document.querySelector('.filter');
    const filterText = document.querySelector('.filter-btn > span');
    const fa_angle_up = document.querySelector('.filter i');
    const filter_input = document.querySelector('.filter-input');
    const country_card = document.querySelector('.country-card');
    const mode= document.querySelector('.mode');
    const card = document.querySelectorAll('.card');
    const error = document.querySelector('.error');
    const searchBox = document.querySelector('.searchBox');
    const regions = document.querySelectorAll('li');
    const more_info = document.querySelector('.more');
    const logoText = document.querySelector('.headerText');
    const body = document.querySelector('body');
    const MainContainer = document.querySelector('.MainContainer');
    const filterSection = document.querySelector('.filter-section')
    const header = document.querySelector('header');
    const loader = document.querySelector('.loadingio-spinner-ellipsis-2rjr55p7286');




    // functions
        function renderCountryInfo(country){

            // destructuring the country object
            const {flags,population,region,subregion,capital,languages,name,tld,currencies,maps} = country;
            // creating Dom element and Appending to it
            myDom.country_card.textContent ='';
            const card = document.createElement('div');
                card.setAttribute('class','info-card');
                const image = document.createElement('img');
                        image.setAttribute('src',`${flags.png}`)
                        image.setAttribute('class','heroImage')
                const infoContainer = document.createElement('div');
                        infoContainer.setAttribute('class','info-content');
                const info = document.createElement('div');
                        info.setAttribute('class','info');      
        
                        const leftContent = document.createElement('div');
                            leftContent.setAttribute('class','leftContent');             
                            const h2 = document.createElement('h2');
                                    h2.textContent = `${name.common} `;
                            const nativeName = document.createElement('p');
                                    nativeName.innerHTML = `Native Name: <span>${name.nativeName[Object.keys(name.nativeName)[0]].common}</span>`;
                            const population1 = document.createElement('p');
                                    population1.innerHTML = `population: <span>${population}</span>`;
                            const region1 = document.createElement('p');
                                    region1.innerHTML = `region: <span>${region}</span>`;
                            const subregion1 = document.createElement('p');
                                    subregion1.innerHTML = `Sub Region: <span>${subregion}</span>`;
                            const capital1 = document.createElement('p');
                                    capital1.innerHTML = `Capital: <span>${capital}</span>`;                
                        leftContent.append(nativeName,population1,region1,subregion1,capital1);
        
                    
                        const rightContent = document.createElement('div');
                            rightContent.setAttribute('class','rightContent');
                            const tld1 = document.createElement('p');
                            tld1.innerHTML = `Top Level Domain: <span>${tld[0]}</span>`;
                            const currencies1 = document.createElement('p');
                            currencies1.innerHTML = `Currencies: <span>${currencies[Object.keys(currencies)[0]].name}</span>`;
                            const languages1 = document.createElement('p');
                            languages1.innerHTML = `Language: <span>${languages[Object.keys(languages)[0]]}</span>`;
                        rightContent.append(tld1,currencies1,languages1);
                        info.append(leftContent,rightContent);
                        
                        const borders = document.createElement('div');
                            borders.setAttribute('class','borders');
                            const p = document.createElement('h3');
                                p.textContent = 'Border Countries: ';
                                borders.appendChild(p);
                            if (country.borders) {
                                for (let i = 0; i < country.borders.length; i++) {
                                    const border = document.createElement('p');
                                        border.textContent = `${country.borders[i]}`;
                                        border.setAttribute('class','borderCountry');
                                        border.addEventListener('click',(e)=>showBorderCountryInfo(e))
                                        borders.appendChild(border);
                                }
                            }else{
                                borders.textContent = '';
                                const p = document.createElement('h4');
                                    p.textContent = ` doesn't have any border countries`;
                                    borders.appendChild(p);
                            }
                            
                            const icon = document.createElement('div');
                                icon.setAttribute('class','icon'); 
                            
                            const map = document.createElement('a');
                                map.innerHTML = "<i class= 'fa-solid fa-location-dot'></i>";
                                map.setAttribute('href',maps.googleMaps); 
                                map.setAttribute('target','_blank');
                            const weather = document.createElement('a');
                                weather.innerHTML = "<i class='fa-solid fa-cloud-sun'></i>";
                                weather.addEventListener('click',()=>weatherInformation.showWeatherInfo(country.capital)); 
                                
                            icon.append(map,weather);      
                            
                        infoContainer.append(h2,info,borders,icon);
                const allInfo = document.createElement('div');
                        allInfo.setAttribute('class','allInfo');
                        allInfo.append(image,infoContainer);
                        
            //appending all created Dom Elements above to country_card element 
            myDom.country_card.append(allInfo);
        }

    
        function fetchData(){
            fetch('https://restcountries.com/v3.1/all')
            .then(Response=>Response.json())
            .then(data=>{
                data.forEach(da=>{
                    if(myDom.searchBox.value == ''){
                        countryApi.render(da);
                    }
                
                })
            })
            .catch(()=>{
                const error = `
            <div class='errors'>  
                <p class="error">Something went wrong!</p>
                <p class="error">Please Check your internet connection!</p>
            </div>  
                `;
                myDom.country_card.insertAdjacentHTML('beforeend',error)
            })
            myDom.searchBox.value = '';
        }

        function show(e){
            let parent = e.target.parentNode;
            let name = parent.querySelector('.countryName').textContent
            fetchDataByCountryName(name);
        }

        function fetchDataByCountryName(countryName){
            fetch(`https://restcountries.com/v3.1/name/${countryName}`)
            .then(response=>response.json())
            .then(country => {
                renderCountryInfo(country[0])
            });
        }

        // render border countries info on click on country code
        function showBorderCountryInfo(e){
            fetch(`https://restcountries.com/v3.1/alpha/${e.target.textContent}`)
        .then(data=>data.json())
        .then(data=>{
            renderCountryInfo(data[0])
        })
        }

            return{
                options,
                error,
                filter_btn,
                fa_angle_up,
                filter_input,
                country_card,
                mode,
                card,
                searchBox,
                regions,
                filterText,
                more_info,
                logoText,
                body,
                MainContainer,
                filterSection,
                header,
                loader,
                renderCountryInfo,
                fetchData,
                show,
                fetchDataByCountryName,
            }
})();




