import './style.css';
import {countryApi} from './apiCalls.js'
import {myDom} from './myDom.js';

// event listners
myDom.searchBox.addEventListener('input',countryApi.searchCountry);
myDom.regions.forEach(list=>{
    list.addEventListener('click',()=>{
        countryApi.filtere(list);
    })

})
myDom.filter_btn.addEventListener('click',showOption);
myDom.mode.addEventListener('click',changeToDarkMode);

// functions
function showOption(){
    myDom.options.classList.toggle('show');
    myDom.fa_angle_up.style.transform=`rotate(-180deg)`;
}

function changeToDarkMode(){
   document.body.classList.toggle('dark');
}

// click the Header to refresh the list 
myDom.logoText.addEventListener('click',()=>{
    myDom.country_card.textContent ='';
    myDom.fetchData()
});


// calling the fetchdata function to fetch all countries from rest countries API
myDom.fetchData()
