 import { myDom } from "./myDom";

 export  const countryApi=(()=>{

    let filteredData = [];
    let searchedData = [];

    // search countries with there name
    function searchCountry(){
        fetch('https://restcountries.com/v3.1/all')
            .then(Response=>Response.json())
            .then(data=>{
                data.forEach(da=>{
                    myDom.country_card.textContent ='';
                    if((da.name.common).toLowerCase().startsWith(myDom.searchBox.value)){
                        searchedData.push(da)
                    }
                })
                searchedData.forEach(data=>{
                    render(data);
                })
                myDom.loader.style.display = 'none';
            })
        searchedData = [];
      }

    // filter countries with there region they located
      function filtere(list){
        myDom.filterText.textContent = list.textContent;
        fetch('https://restcountries.com/v2/all')
            .then(Response=>Response.json())
            .then(data=>{
                data.forEach(da=>{
                    myDom.country_card.textContent ='';
                    if(da.region == list.textContent){
                        filteredData.push(da)
                    }
                })
                filteredData.forEach(data=>{
                    render(data);
                })
                myDom.loader.style.display = 'none';
            })
            console.log(filteredData);
            filteredData = [];
      }


    //  render each country info in the dom element
    // country cards
    function render(data){
        const card = document.createElement('div');
              card.setAttribute('class','card');
              const image = document.createElement('img');
                    image.setAttribute('src',`${data.flags.png}`)
              const h2 = document.createElement('h2');
                    h2.setAttribute('class','countryName');
                    h2.textContent = `${[...data.name.common].splice(0,15).join('')}`;
              const p1 = document.createElement('p');
                    p1.setAttribute('class','pop');
                    p1.innerHTML = `Population  <span>${(data.population/1000000).toFixed(1)}mil</span>`;
              const p2 = document.createElement('p');
                    p2.setAttribute('class','rigion');
                    p2.innerHTML = `Region  <span>${data.region}</span>`; 
              const p3 = document.createElement('p');
                    p3.setAttribute('class','capital');
                    p3.innerHTML = `Capital  <span>${data.capital}</span>`;
              const a = document.createElement('a');
                    a.setAttribute('class','more');
                    a.innerHTML = `more info`;
                    a.addEventListener('click',(e)=>myDom.show(e))  
            card.append(image,h2,p1,p2,p3,a)             
            myDom.country_card.appendChild(card);
    }

    return{
        searchCountry,
        filtere,
        render,
    }
})()


